import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const Header = () => (
    <div id="header"><i className="fas fa-utensils"></i>Recipe App</div>
)

const DirectoryView = (props) => (
    <div id="directory">
        <SearchForm 
            searchVal={props.searchVal} 
            handleChange={props.handleChange}
            selectRecipes={props.selectRecipes}
        />
        {props.recipeList.map(recipe => <RecipeCard name={recipe.name} clickHandler={props.clickHandler} id={recipe.id} key={recipe.id}/>)}
    </div>
)

const SearchForm = (props) => (
    <div>
        <form>
            <input value={props.searchVal} onChange={props.handleChange} />
            <button onClick={props.selectRecipes}>Search</button>
        </form>
    </div>
)
const RecipeCard = (props) => (
    <div>
        <h3 onClick={()=> props.clickHandler(props.id)}>{props.name}</h3>
    </div>
)

const DetailView = (props) => (
   
    <div id="details"> 
        
        <p>{props.recipe.name}</p>
        <p>{props.recipe.ingredients}</p>
        <p>{props.recipe.instructions}</p>
    </div>
)

class App extends React.Component{
    state = {

        recipeList:[
            {
                id: 1,
        name: 'turkey + stuff',
        ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
        instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery']
            },
            {
                id: 2,
        name: 'turkey salad',
        ingredients: ['turkey', 'mayonnaise', 'pickles', 'celery', 'potato'],
        instructions: ['chop leftover turkey til fine', 'mix mayonnaise', 'add additional ingredients']
            },
            {
                id: 3,
        name: 'bread',
        ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
        instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery']
            },
            {
                id: 4,
        name: 'white rice',
        ingredients: ['1 cup white rice', '1 cup water'],
        instructions: ['bring water to boil', 'add rice', 'cover', 'turn off heat']
            },
            {
                id: 5,
                name: 'yellow rice',
                ingredients: ['1 cup yellow rice', '1 cup water'],
                instructions: ['bring water to boil', 'add rice', 'cover', 'turn off heat']
            },
            {
                id: 6,
                name: 'dirty rice',
                ingredients: ['1 cup white rice', '1 cup water', 'dirt'],
                instructions: ['bring water to boil', 'add rice', 'add dirt', 'cover', 'turn off heat']
            },
            {
                id: 7,
        name: 'turkey + stuff',
        ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
        instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery']
            },
            {
                id: 8,
        name: 'turkey + stuff',
        ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
        instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery']
            },
            {
                id: 9,
        name: 'turkey + stuff',
        ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
        instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery']
            },
            {
                id: 10,
        name: 'turkey + stuff',
        ingredients: ['turkey', 'mustard', 'greens', 'celery', 'potato'],
        instructions: ['pre-heat over to 350', 'tickle the turkey', 'cut some celery']
            },

        ],
        searchVal: '',
        selectedRecipes: [],
        recipe: {}
    }

        handleChange = (event) => {
           this.setState({ searchVal: event.target.value})
        }
       
        selectRecipes = (event) =>  {
            event.preventDefault();
            const recipearray  = this.state.recipeList.filter(recipe => recipe.name.includes(this.state.searchVal))
                this.setState({ selectedRecipes: recipearray, recipe: recipearray[0]})
            }
        selectARecipe = (id) => {
            console.log(id);
            this.setState({ recipe: this.state.selectedRecipes.find(recipe => recipe.id === id) })
        }

    render(){
     
        return(
            <div id="container">

                <Header
                />
                <DirectoryView 
                    recipeList={this.state.selectedRecipes}
                    handleChange={this.handleChange}
                    searchVal={this.state.searchVal}
                    selectRecipes={this.selectRecipes}
                    clickHandler={this.selectARecipe}
                />
                <DetailView
                    recipe={this.state.recipe}
                    
                />
            </div>
            
        )
    }


}




ReactDOM.render(<App />, document.getElementById('root'));

